.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Custom styles */

body {
	font-family: "Open Sans", sans-serif;
	font-weight: 300;
}

a {
    text-decoration: none;
}

.main {
	padding: 50px;
}

.flex {
	display: flex;
}

.container {
	padding: 50px;
}

.phone-container {
	width: 50%;
}

.phone-container img {
	
}

.description-container {
	width: 50%;
}

.description-container h1 {
	font-size: 45px;
    font-weight: 400;
    line-height: 61px;
    margin-bottom: 40px;
}

.description-container p {
	font-size: 20px;
	line-height: 25px;
    margin-bottom: 50px;
    color: #555;
}

.logo-container img {
	width: 50px;
	height: 50px;
	border-radius: 10px;
}

.store-link {
	background: #333332;
	border-color: #333332;
	padding: 20px 40px;
	font-size: 18px;
	color: #fff;
	border-radius: 10px;
	margin-right: 20px;
}

.footer {
	background: #2F3542;
	padding: 50px;
    text-align: center;
}

.footer ul {
	margin: 0;
    padding: 0;
    display: inline-flex;
    list-style: none;
}

.footer ul li {
	padding: 0 10px;
}

.footer ul li a {
    font-weight: bold;
    text-transform: uppercase;
    font-size: 14px;
    color: #fff;
	transition: 0.5s;
}

.footer ul li a:hover {
	border-bottom: 2px solid #fff;
}